import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../api/http.service'
import { Router } from '@angular/router';
@Component({
  selector: 'app-hospitality',
  templateUrl: './hospitality.page.html',
  styleUrls: ['./hospitality.page.scss'],
  providers: [HttpService]
})
export class HospitalityPage implements OnInit {
  currentColor:any;
  response:any;
  contentdata:any;
  theme:any;
  constructor(public shared: HttpService,public router: Router) {
    this.currentColor = 'light';
    this.theme = this.router.getCurrentNavigation().extras.state.theme;
   
   }

   ionViewDidEnter() {
    if (navigator.onLine) {
            this.shared.ServiceCallHost.ServiceCallRequestHost(this.theme).then((res) => { 
              // debugger
              this.response=res;
              this.contentdata=this.response.content.hospitality
              }, err => {
               
              })
    
          }else{
            this.shared.displayNetworkUpdate('No Internet Connection')
          }
        
      }



  ngOnInit() {
  }

}
